import React, { createContext, useContext } from 'react';
import { useUserData } from '../hooks/useUserData';
import { useAuthValue } from './AuthContext';

export const UserDataContext = createContext();

export const UserDataProvider = ({ children }) => {
  const { currentUser } = useAuthValue();
  const { userData, setUserData } = useUserData(currentUser && currentUser.uid);

  const getBMRValue = () => {
    let bmrValue;

    if (userData) {
      if (userData.gender === 'male') {
        bmrValue =
          66.473 +
          13.7516 * userData.weight +
          5 * userData.height -
          6.755 * userData.age;
        // console.log(`bmr pro muže: ${Math.round(bmrValue)}kcal`);
      } else if (userData.gender === 'female') {
        bmrValue =
          655.1 +
          9.654 * userData.weight +
          1.84 * userData.height -
          4.67 * userData.age;
        // console.log(`bmr pro ženskou: ${Math.round(bmrValue)}kcal`);
      }
    }

    // console.log('bmrValue', bmrValue);
    return Math.round(bmrValue);
  };

  const getTEEValue = () => {
    let teeValue;

    if (userData) {
      teeValue = userData.palRatio * getBMRValue();
    }
    // console.log(`předpokladany denní vydej: ${Math.round(teeValue)}kcal`);

    // console.log('teeValue', teeValue);
    return Math.round(teeValue);
  };

  return (
    <UserDataContext.Provider
      value={{ userData, setUserData, getBMRValue, getTEEValue }}
    >
      {children}
    </UserDataContext.Provider>
  );
};

export const useUserDataValue = () => useContext(UserDataContext);
