import './App.scss';
import React, { useState, useRef, useEffect } from 'react';
import { useAuthValue } from './context/AuthContext';
import { useLocation } from 'react-router-dom';
import { Switch, Route, Redirect } from 'react-router-dom';
import { firebase } from './firebase';
import { useHistory } from 'react-router-dom';
import {
  RecipePage,
  MenuPage,
  ProfilePage,
  IntroPage,
  LoginPage,
  RegisterPage,
} from './pages/main';
import { Header, BottomNav } from './compontents/layout';
import { PrivateRoute, AlertBanner } from './compontents';
import { UserDataProvider, useUserDataValue } from './context/UserDataContext';

function App() {
  const [refresh, setRefresh] = useState(false);
  const header = useRef();
  const { currentUser } = useAuthValue();
  const { userData } = useUserDataValue();
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    console.log('currentUser', currentUser);
    console.log('userData', userData);
    // if (location.pathname === '/login' || location.pathname === '/register') {
    //   setHideNav(true);
    //   console.log('hidden');
    // } else {
    //   setHideNav(false);
    // }
    if (location.pathname === '/') {
      history.push('/create-recipe');
    }
    if (
      !currentUser &&
      location.pathname !== '/login' &&
      location.pathname !== '/register'
    ) {
      history.push('/login');
    }
  }, [refresh, history]);

  return (
    <Switch>
      <div className='app'>
        <AlertBanner />
        <Header passRef={header} />
        <div className='app__content'>
          <PrivateRoute exact path='/' />
          {/* <Route path='/'>
                <Redirect to='/create-recipe' />
              </Route> */}
          <Route path='/my-profile'>
            <ProfilePage />
          </Route>
          <Route path='/create-recipe'>
            <RecipePage header={header} />
          </Route>
          <Route path='/my-recipes'>
            <MenuPage header={header} />
          </Route>
        </div>
        <BottomNav />

        <Route path='/login'>
          <LoginPage />
        </Route>
        <Route path='/register'>
          <RegisterPage />
        </Route>
        <Route path='/create-user'>
          <IntroPage setRefresh={setRefresh} />
        </Route>
      </div>
    </Switch>
  );
}

export default App;
