/* eslint-disable no-nested-ternary */
import { useState, useEffect } from 'react';
import { firebase } from '../firebase';
import { sortObjectKeys } from '../helpers/sortObjectKeys';

export const useUserData = (uid = '') => {
  const [currentUserData, setCurrentUserData] = useState([]);
  const [userData, setUserData] = useState([]);
  const [retrigger, setRetrigger] = useState(false);

  useEffect(() => {
    //! create a callback that checks wheter the user (UID) has a UserData document => returns an boolean value => insert to useEffect

    console.log('i ran yyyooyyoo');

    firebase
      .firestore()
      .collection('userData')
      .where('userId', '==', uid)
      .get()
      .then((snapshot) => {
        const allUserData = snapshot.docs.map((project) => ({
          ...project.data(),
          docId: project.id,
        }));

        const newUserData = sortObjectKeys(currentUserData);
        const newAllUserData = sortObjectKeys(allUserData);

        if (JSON.stringify(newAllUserData) !== JSON.stringify(newUserData)) {
          setCurrentUserData(newAllUserData);
          setUserData(newAllUserData[0]);
          console.log('newAllUserData', newAllUserData);
        }
      })
      .catch((error) => {
        console.log('Error getting documents: ', error);
      });
  }, [currentUserData, uid]);

  return { userData, setUserData, setRetrigger };
};
